import React from 'react';

import { Toaster } from 'react-hot-toast';
import { Link } from 'react-router-dom';

import { ReactComponent as CartSvg } from '/public/img/cart.svg';
import { ReactComponent as ArrowLeftSvg } from '/public/img/grey-arrow-left.svg';
import { ReactComponent as TrashSvg } from '/public/img/trash.svg';

import CartItem from 'components/CartItem/CartItem';
import EmptyCart from 'components/EmptyCart/EmptyCart';
import { AnimatePresence, motion } from 'framer-motion';
import { useAppDispatch, useAppSelector } from 'hooks/redux';
import { clearCart } from 'store/cart/cartSlice';
import {
  selectCartItems,
  selectCartTotalItems,
  selectCartTotalSum,
} from 'store/cart/selectors';

const Cart: React.FC = () => {
  const dispatch = useAppDispatch();
  const items = useAppSelector(selectCartItems);
  const totalSum = useAppSelector(selectCartTotalSum);
  const totalItems = useAppSelector(selectCartTotalItems);

  const handleClearCartClick = () => {
    dispatch(clearCart());
  };

  return (
    <>
      <AnimatePresence>
        <motion.div
          key='cart'
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 1 }}
        >
          {!items.length ? (
            <EmptyCart />
          ) : (
            <div className='container container--cart'>
              <Toaster />
              <div className='cart'>
                <div className='cart__top'>
                  <h2 className='content__title'>
                    <CartSvg />
                    –ö–æ—Ä–∑–∏–Ω–∞
                  </h2>
                  <div className='cart__clear'>
                    <TrashSvg />
                    <span onClick={handleClearCartClick}>–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É</span>
                  </div>
                </div>
                <div className='content__items'>
                  {items.map((item) => (
                    <CartItem key={item.id} {...item} />
                  ))}
                </div>
                <div className='cart__bottom'>
                  <div className='cart__bottom-details'>
                    <span>
                      –í—Å–µ–≥–æ –ø–∏—Ü—Ü: <b>{totalItems} —à—Ç.</b>
                      <span></span>
                    </span>
                    –°—É–º–º–∞ –∑–∞–∫–∞–∑–∞: <b>{totalSum} ‚ÇΩ</b>
                  </div>
                  <div className='cart__bottom-buttons'>
                    <Link
                      to='/'
                      className='button button--outline button--add go-back-btn'
                    >
                      <ArrowLeftSvg />
                      <span>–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥</span>
                    </Link>
                    <div className='button pay-btn'>
                      <span>–û–ø–ª–∞—Ç–∏—Ç—å —Å–µ–π—á–∞—Å</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </motion.div>
      </AnimatePresence>
    </>
  );
};

export default Cart;
